.match-preview {
    padding: 10px 16px;
    margin: auto;
    width: 500px;
    border-bottom: 1px solid white;
}

.match-preview:hover {
    box-shadow: 1px 3px 5px black;
}

.match-preview h2 {
    font-size: 20px;
    color: red;
    margin-bottom: 8px;
}
.create {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
  }
  .create label {
    text-align: left;
    display: block;
  }
  .create h2 {
    margin: auto;
    text-align: center;
    padding: 10px;
    border-radius: 16px;
    width: 300px;
    border: 0;
    background: tomato;
  }
  .create input, .create select {
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
  }
  .create button {
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
  }
